"use strict";
import { ScrollBarStyles } from "components/Common/styles";
import { LoadingBubble } from "components/Tokens/loading";
import { Box } from "components/deprecated/Box";
import { useIsMobile } from "hooks/screenSize/useIsMobile";
import styled from "lib/styled-components";
import { Column, Row } from "nft/components/Flex";
import { XMarkIcon } from "nft/components/icons";
import { Input } from "nft/components/layout/Input";
import { WALLET_COLLECTIONS_PAGINATION_LIMIT } from "nft/components/profile/view/ProfilePage";
import * as styles from "nft/components/profile/view/ProfilePage.css";
import { subhead } from "nft/css/common.css";
import { themeVars } from "nft/css/sprinkles.css";
import { useFiltersExpanded, useWalletCollections } from "nft/hooks";
import { useCallback, useEffect, useMemo, useState } from "react";
import AutoSizer from "react-virtualized-auto-sizer";
import { FixedSizeList } from "react-window";
import InfiniteLoader from "react-window-infinite-loader";
import { ThemedText } from "theme/components";
import { LabeledCheckbox } from "ui/src";
import noop from "utilities/src/react/noop";
const COLLECTION_ROW_HEIGHT = 44;
const ItemsContainer = styled(Column)`
  ${ScrollBarStyles}
  height: 100vh;
`;
const LongLoadingBubble = styled(LoadingBubble)`
  min-height: 15px;
  width: 75%;
`;
const SmallLoadingBubble = styled(LoadingBubble)`
  height: 20px;
  width: 20px;
  margin-right: 8px;
`;
const MobileMenuHeader = styled(Row)`
  justify-content: space-between;
  padding-bottom: 8px;
`;
const LoadingCollectionItem = ({ style }) => {
  return <Row display="flex" justifyContent="space-between" style={style} paddingLeft="12" paddingRight="16"><Row display="flex" flex="1"><SmallLoadingBubble /><LongLoadingBubble /></Row><Box as="span" borderColor="surface3" aria-hidden="true" /></Row>;
};
export const FilterSidebar = ({
  fetchNextPage,
  hasNextPage,
  isFetchingNextPage,
  walletCollections
}) => {
  const collectionFilters = useWalletCollections((state) => state.collectionFilters);
  const setCollectionFilters = useWalletCollections((state) => state.setCollectionFilters);
  const [isFiltersExpanded, setFiltersExpanded] = useFiltersExpanded();
  const isMobile = useIsMobile();
  const hideSearch = useMemo(
    () => walletCollections && walletCollections?.length >= WALLET_COLLECTIONS_PAGINATION_LIMIT || isFetchingNextPage,
    [walletCollections, isFetchingNextPage]
  );
  return <Box
    position={{ sm: "fixed", md: "sticky" }}
    top={{ sm: "0", md: "72" }}
    left={{ sm: "0", md: "unset" }}
    width={{ sm: "full", md: "332", lg: "332" }}
    height={{ sm: "full", md: "auto" }}
    zIndex={{ sm: "modal", md: "auto" }}
    display={isFiltersExpanded ? "flex" : "none"}
    style={{ transform: isMobile ? void 0 : `translateX(${isFiltersExpanded ? 0 : -360}px)` }}
  ><Box
    paddingTop={{ sm: "24", md: "0" }}
    paddingLeft={{ sm: "16", md: "0" }}
    paddingRight="16"
    width={{ sm: "full", md: "332", lg: "332" }}
  >{isMobile && <MobileMenuHeader><ThemedText.HeadlineSmall>Filter</ThemedText.HeadlineSmall><XMarkIcon
    height={28}
    width={28}
    fill={themeVars.colors.neutral1}
    onClick={() => setFiltersExpanded(false)}
  /></MobileMenuHeader>}<CollectionSelect
    collections={walletCollections}
    collectionFilters={collectionFilters}
    setCollectionFilters={setCollectionFilters}
    fetchNextPage={fetchNextPage}
    hasNextPage={hasNextPage}
    isFetchingNextPage={isFetchingNextPage}
    hideSearch={hideSearch}
  /></Box></Box>;
};
const CollectionSelect = ({
  collections,
  collectionFilters,
  setCollectionFilters,
  fetchNextPage,
  hasNextPage,
  isFetchingNextPage,
  hideSearch
}) => {
  const [collectionSearchText, setCollectionSearchText] = useState("");
  const [displayCollections, setDisplayCollections] = useState(collections);
  useEffect(() => {
    if (collectionSearchText) {
      const filtered = collections.filter(
        (collection) => collection.name?.toLowerCase().includes(collectionSearchText.toLowerCase())
      );
      setDisplayCollections(filtered);
    } else {
      setDisplayCollections(collections);
    }
  }, [collectionSearchText, collections]);
  const itemKey = useCallback((index, data) => {
    if (!data) {
      return index;
    }
    const collection = data[index];
    return `${collection.address}_${index}`;
  }, []);
  const itemCount = hasNextPage ? displayCollections.length + 1 : displayCollections.length;
  const loadMoreItems = isFetchingNextPage ? noop : fetchNextPage;
  const isItemLoaded = useCallback(
    (index) => !hasNextPage || index < displayCollections.length,
    [displayCollections.length, hasNextPage]
  );
  const CollectionFilterRow = useCallback(
    ({ index, style }) => {
      const collection = !!displayCollections && displayCollections[index];
      if (!collection || isFetchingNextPage) {
        return <LoadingCollectionItem style={style} key={index} />;
      }
      return <CollectionItem
        style={style}
        key={itemKey(index, displayCollections)}
        collection={displayCollections[index]}
        collectionFilters={collectionFilters}
        setCollectionFilters={setCollectionFilters}
      />;
    },
    [displayCollections, isFetchingNextPage, itemKey, collectionFilters, setCollectionFilters]
  );
  return <><Box className={subhead} marginTop="12" marginBottom="16" width="276">
        Collections
      </Box><Box paddingBottom="12" borderRadius="8"><Column as="ul" paddingLeft="0" gap="10" style={{ maxHeight: "80vh" }}>{!hideSearch && <CollectionFilterSearch
    collectionSearchText={collectionSearchText}
    setCollectionSearchText={setCollectionSearchText}
  />}<ItemsContainer><AutoSizer disableWidth>{({ height }) => <InfiniteLoader isItemLoaded={isItemLoaded} itemCount={itemCount} loadMoreItems={loadMoreItems}>{({
    onItemsRendered,
    ref
  }) => <FixedSizeList
    height={height}
    width="100%"
    itemCount={itemCount}
    itemSize={COLLECTION_ROW_HEIGHT}
    onItemsRendered={onItemsRendered}
    itemKey={itemKey}
    ref={ref}
  >{CollectionFilterRow}</FixedSizeList>}</InfiniteLoader>}</AutoSizer></ItemsContainer></Column></Box></>;
};
const CollectionFilterSearch = ({
  collectionSearchText,
  setCollectionSearchText
}) => {
  return <Input
    placeholder="Search"
    marginTop="8"
    marginBottom="8"
    autoComplete="off"
    position="static"
    width="full"
    value={collectionSearchText}
    onChange={(e) => setCollectionSearchText(e.currentTarget.value)}
  />;
};
const CollectionItem = ({
  collection,
  collectionFilters,
  setCollectionFilters,
  style
}) => {
  const [isCheckboxSelected, setCheckboxSelected] = useState(false);
  const isChecked = useCallback(
    (address) => {
      return collectionFilters.some((collection2) => collection2 === address);
    },
    [collectionFilters]
  );
  const handleCheckbox = () => {
    setCheckboxSelected(!isCheckboxSelected);
    setCollectionFilters(collection.address);
  };
  return <Row
    maxWidth="full"
    overflowX="hidden"
    overflowY="hidden"
    fontWeight="book"
    className={styles.subRowHover}
    justifyContent="space-between"
    cursor="pointer"
    paddingLeft="12"
    paddingRight="16"
    borderRadius="12"
    style={{
      paddingBottom: "22px",
      paddingTop: "22px",
      ...style
    }}
    maxHeight={`${COLLECTION_ROW_HEIGHT}`}
    as="li"
    onClick={handleCheckbox}
  ><Row><Box as="img" borderRadius="round" width="20" height="20" src={collection.image} /><Box
    as="span"
    whiteSpace="nowrap"
    textOverflow="ellipsis"
    overflow="hidden"
    paddingLeft="12"
    paddingRight="14"
    style={{ minHeight: 15, maxWidth: "180px" }}
  >{collection.name}{" "}</Box></Row><LabeledCheckbox
    checked={isChecked(collection.address)}
    onCheckPressed={handleCheckbox}
    text={String(collection.count)}
  /></Row>;
};
