"use strict";
import Column from "components/deprecated/Column";
import Row from "components/deprecated/Row";
import styled, { css, useTheme } from "lib/styled-components";
import { VerifiedIcon } from "nft/components/icons";
import { MarketplaceRow } from "nft/components/profile/list/MarketplaceRow";
import { useSellAsset } from "nft/hooks";
import { useEffect, useReducer, useState } from "react";
import { Trash2 } from "react-feather";
import { BREAKPOINTS } from "theme";
import { ThemedText } from "theme/components";
const IMAGE_THUMBNAIL_SIZE = 60;
const NFTListRowWrapper = styled(Row)`
  padding: 24px 0px;
  align-items: center;
  border-radius: 8px;

  &:hover {
    background: ${({ theme }) => theme.surface3};
  }
`;
const RemoveIconContainer = styled.div`
  width: ${IMAGE_THUMBNAIL_SIZE}px;
  height: ${IMAGE_THUMBNAIL_SIZE}px;
  padding-left: 12px;
  align-self: flex-start;
  align-items: center;
  display: flex;

  @media screen and (max-width: ${BREAKPOINTS.sm}px) {
    display: none;
  }

  &:hover {
    opacity: ${({ theme }) => theme.opacity.hover};
  }
`;
const NFTInfoWrapper = styled(Row)`
  align-items: center;
  min-width: 0px;
  flex: 2;
  margin-bottom: auto;

  @media screen and (min-width: ${BREAKPOINTS.md}px) {
    flex: 1.5;
  }
`;
const NFTImage = styled.img`
  width: ${IMAGE_THUMBNAIL_SIZE}px;
  height: ${IMAGE_THUMBNAIL_SIZE}px;
  border-radius: 8px;
  margin-right: 8px;
`;
const HideTextOverflow = css`
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
`;
const TokenInfoWrapper = styled(Column)`
  margin-right: 8px;
  min-width: 0px;
`;
const TokenName = styled.div`
  font-weight: 535;
  font-size: 16px;
  line-height: 24px;
  ${HideTextOverflow}
`;
const CollectionName = styled(ThemedText.BodySmall)`
  color: ${({ theme }) => theme.neutral2};
  line-height: 20px;
  ${HideTextOverflow};
`;
const MarketPlaceRowWrapper = styled(Column)`
  gap: 24px;
  flex: 1.5;
  margin-right: 12px;
  padding: 6px 0px;

  @media screen and (min-width: ${BREAKPOINTS.md}px) {
    flex: 2;
  }

  @media screen and (min-width: ${BREAKPOINTS.md}px) {
    flex: 3;
  }
`;
export const NFTListRow = ({
  asset,
  globalPriceMethod,
  globalPrice,
  setGlobalPrice,
  selectedMarkets
}) => {
  const [expandMarketplaceRows, toggleExpandMarketplaceRows] = useReducer((s) => !s, false);
  const removeAsset = useSellAsset((state) => state.removeSellAsset);
  const [localMarkets, setLocalMarkets] = useState([]);
  const [hovered, toggleHovered] = useReducer((s) => !s, false);
  const theme = useTheme();
  useEffect(() => {
    setLocalMarkets(JSON.parse(JSON.stringify(selectedMarkets)));
  }, [selectedMarkets]);
  return <NFTListRowWrapper
    onMouseEnter={() => {
      !hovered && toggleHovered();
    }}
    onMouseLeave={() => {
      hovered && toggleHovered();
    }}
  ><RemoveIconContainer>{hovered && <Trash2
    size={20}
    color={theme.neutral2}
    cursor="pointer"
    onClick={() => {
      removeAsset(asset);
    }}
  />}</RemoveIconContainer><NFTInfoWrapper><NFTImage alt={asset.name} src={asset.imageUrl || "/nft/svgs/image-placeholder.svg"} /><TokenInfoWrapper><TokenName>{asset.name ? asset.name : `#${asset.tokenId}`}</TokenName><CollectionName>{asset.collection?.name}{asset.collectionIsVerified && <VerifiedIcon style={{ marginBottom: "-5px" }} />}</CollectionName></TokenInfoWrapper></NFTInfoWrapper><MarketPlaceRowWrapper>{expandMarketplaceRows && localMarkets.length > 1 ? localMarkets.map((market) => {
    return <MarketplaceRow
      globalPriceMethod={globalPriceMethod}
      globalPrice={globalPrice}
      setGlobalPrice={setGlobalPrice}
      selectedMarkets={[market]}
      removeMarket={() => setLocalMarkets(localMarkets.filter((oldMarket) => oldMarket.name !== market.name))}
      asset={asset}
      key={asset.name + market.name}
      expandMarketplaceRows={expandMarketplaceRows}
      rowHovered={hovered}
      toggleExpandMarketplaceRows={toggleExpandMarketplaceRows}
    />;
  }) : <MarketplaceRow
    globalPriceMethod={globalPriceMethod}
    globalPrice={globalPrice}
    setGlobalPrice={setGlobalPrice}
    selectedMarkets={localMarkets}
    asset={asset}
    rowHovered={hovered}
    toggleExpandMarketplaceRows={toggleExpandMarketplaceRows}
  />}</MarketPlaceRowWrapper></NFTListRowWrapper>;
};
