"use strict";
import { SMALL_MEDIA_BREAKPOINT } from "components/Tokens/constants";
import { useOnClickOutside } from "hooks/useOnClickOutside";
import styled from "lib/styled-components";
import { Column, Row } from "nft/components/Flex";
import { ChevronUpIcon } from "nft/components/icons";
import { buttonTextMedium, caption } from "nft/css/common.css";
import { themeVars } from "nft/css/sprinkles.css";
import { getMarketplaceIcon } from "nft/utils";
import { ListingMarkets } from "nft/utils/listNfts";
import { useMemo, useReducer, useRef } from "react";
import { ThemedText } from "theme/components";
import { Z_INDEX } from "theme/zIndex";
import { Checkbox } from "ui/src";
const MarketplaceRowWrapper = styled(Row)`
  gap: 6px;
  height: 44px;
  width: 100%;
  cursor: pointer;
  justify-content: space-between;
  padding: 0px 16px;
  &:hover {
    background-color: ${({ theme }) => theme.surface3};
  }
  border-radius: 12px;
`;
const FeeText = styled.div`
  color: ${({ theme }) => theme.neutral2};
`;
const MarketplaceRow = ({ market, setSelectedMarkets, selectedMarkets }) => {
  const isSelected = selectedMarkets.includes(market);
  const toggleSelected = () => {
    if (selectedMarkets.length === 1 && isSelected) {
      return;
    }
    isSelected ? setSelectedMarkets(selectedMarkets.filter((selected) => selected !== market)) : setSelectedMarkets([...selectedMarkets, market]);
  };
  return <MarketplaceRowWrapper onClick={toggleSelected}><Row gap="12" onClick={toggleSelected}>{getMarketplaceIcon(market.name, "24")}<Column><ThemedText.BodyPrimary>{market.name}</ThemedText.BodyPrimary><FeeText className={caption}>{market.fee}% fee</FeeText></Column></Row><Checkbox checked={isSelected} onPress={toggleSelected} variant="branded" /></MarketplaceRowWrapper>;
};
const HeaderButtonWrap = styled(Row)`
  padding: 12px;
  border-radius: 12px;
  width: 180px;
  justify-content: space-between;
  background: ${({ theme }) => theme.surface3};
  cursor: pointer;
  &:hover {
    opacity: ${({ theme }) => theme.opacity.hover};
  }
  @media screen and (min-width: ${SMALL_MEDIA_BREAKPOINT}) {
    width: 220px;
  }
`;
const HeaderButtonContentWrapper = styled.div`
  display: flex;
`;
const MarketIcon = styled.div`
  height: 20px;
  width: 20px;
  margin-right: 8px;
  outline: 1px solid ${({ theme }) => theme.surface3};
  border-radius: 4px;
  z-index: ${({ index, totalSelected }) => totalSelected - index};
  margin-left: ${({ index }) => `${index === 0 ? 0 : -18}px`};
`;
const Chevron = styled(ChevronUpIcon)`
  height: 20px;
  width: 20px;
  fill: ${({ theme }) => theme.neutral1};
  transition: ${({
  theme: {
    transition: { duration }
  }
}) => `${duration.fast} transform`};
  transform: ${({ isOpen }) => `rotate(${isOpen ? 0 : 180}deg)`};
`;
const ModalWrapper = styled.div`
  display: flex;
  flex-direction: column;
  position: relative;
`;
const DropdownWrapper = styled(Column)`
  padding: 16px 0px;
  background-color: ${({ theme }) => theme.surface1};
  display: ${({ isOpen }) => isOpen ? "flex" : "none"};
  position: absolute;
  top: 52px;
  width: 100%;
  border-radius: 12px;
  gap: 12px;
  z-index: ${Z_INDEX.modalBackdrop};
  box-shadow: ${({ theme }) => theme.deprecated_deepShadow};
  border: 0.5px solid ${({ theme }) => theme.surface3};
`;
export const SelectMarketplacesDropdown = ({
  setSelectedMarkets,
  selectedMarkets
}) => {
  const [isOpen, toggleIsOpen] = useReducer((s) => !s, false);
  const dropdownDisplayText = useMemo(
    () => selectedMarkets.length === 1 ? selectedMarkets[0].name : "Multiple",
    [selectedMarkets]
  );
  const ref = useRef(null);
  useOnClickOutside(ref, () => isOpen && toggleIsOpen());
  return <ModalWrapper ref={ref}><HeaderButtonWrap className={buttonTextMedium} onClick={toggleIsOpen}><HeaderButtonContentWrapper>{selectedMarkets.map((market, index) => {
    return <MarketIcon key={index} totalSelected={selectedMarkets.length} index={index}>{getMarketplaceIcon(market.name, "20")}</MarketIcon>;
  })}{dropdownDisplayText}</HeaderButtonContentWrapper><Chevron isOpen={isOpen} secondaryColor={themeVars.colors.neutral1} /></HeaderButtonWrap><DropdownWrapper isOpen={isOpen}>{ListingMarkets.map((market) => {
    return MarketplaceRow({ market, setSelectedMarkets, selectedMarkets });
  })}</DropdownWrapper></ModalWrapper>;
};
