"use strict";
import { defaultAbiCoder } from "@ethersproject/abi";
import { BigNumber } from "@ethersproject/bignumber";
import { hexZeroPad } from "@ethersproject/bytes";
import { AddressZero } from "@ethersproject/constants";
import { keccak256 } from "@ethersproject/keccak256";
import { randomBytes } from "@ethersproject/random";
import { NftStandard } from "uniswap/src/data/graphql/uniswap-data-api/__generated__/types-and-hooks";
const dataParamType = `tuple(address token, uint256 tokenId, uint256 amount)[]`;
const orderItemParamType = `tuple(uint256 price, bytes data)`;
const orderParamTypes = [
  `uint256`,
  `address`,
  `uint256`,
  `uint256`,
  `uint256`,
  `uint256`,
  `address`,
  `bytes`,
  `uint256`,
  `${orderItemParamType}[]`
];
const orderParamType = `tuple(uint256 salt, address user, uint256 network, uint256 intent, uint256 delegateType, uint256 deadline, address currency, bytes dataMask, ${orderItemParamType}[] items, bytes32 r, bytes32 s, uint8 v, uint8 signVersion)`;
const randomSalt = () => {
  const randomHex = BigNumber.from(randomBytes(16)).toHexString();
  return hexZeroPad(randomHex, 64);
};
const encodeItemData = (data) => {
  return defaultAbiCoder.encode([dataParamType], [data]);
};
export const signOrderData = async (web3Provider, order) => {
  const orderData = defaultAbiCoder.encode(orderParamTypes, [
    order.salt,
    order.user,
    order.network,
    order.intent,
    order.delegateType,
    order.deadline,
    order.currency,
    order.dataMask,
    order.items.length,
    order.items
  ]);
  const orderHash = keccak256(orderData);
  const orderSig = await web3Provider.send("personal_sign", [orderHash, order.user]);
  order.r = `0x${orderSig.slice(2, 66)}`;
  order.s = `0x${orderSig.slice(66, 130)}`;
  order.v = parseInt(orderSig.slice(130, 132), 16);
  fixSignature(order);
};
const fixSignature = (data) => {
  if (data.v < 27) {
    data.v = data.v + 27;
  }
};
export const encodeOrder = (order) => {
  return defaultAbiCoder.encode([orderParamType], [order]);
};
export const createSellOrder = (user, deadline, items, nftStandard = NftStandard.Erc721) => {
  const salt = randomSalt();
  const network = 1;
  const intent = 1;
  const delegateType = nftStandard === NftStandard.Erc721 ? 1 : 2;
  const currency = AddressZero;
  return {
    salt,
    user,
    network,
    intent,
    delegateType,
    deadline,
    currency,
    dataMask: "0x",
    items: items.map((item) => ({
      price: item.price,
      data: encodeItemData(item.tokens)
    })),
    r: "",
    s: "",
    v: 0,
    signVersion: 1
  };
};
