"use strict";
import Column from "components/deprecated/Column";
import Row from "components/deprecated/Row";
import styled, { useTheme } from "lib/styled-components";
import { Check } from "react-feather";
import { ThemedText } from "theme/components";
const DropdownWrapper = styled(Column)`
  gap: 4px;
  background: ${({ theme }) => theme.surface1};
  padding: 8px;
  width: ${({ $width }) => $width}px;
  border-radius: 12px;
  box-shadow: ${({ theme }) => theme.deprecated_deepShadow};
  border: 1px solid ${({ theme }) => theme.surface3};
`;
const DropdownRow = styled(Row)`
  justify-content: space-between;
  padding: 8px;
  cursor: pointer;
  border-radius: 12px;

  &:hover {
    background: ${({ theme }) => theme.surface3};
  }
`;
export const Dropdown = ({ dropDownOptions, width }) => {
  const theme = useTheme();
  return <DropdownWrapper $width={width}>{dropDownOptions.map((option) => <DropdownRow key={option.displayText} onClick={option.onClick}><ThemedText.BodyPrimary lineHeight="24px">{option.displayText}</ThemedText.BodyPrimary>{option.isSelected && <Check height={20} width={20} color={theme.accent1} />}</DropdownRow>)}</DropdownWrapper>;
};
