"use strict";
import { SupportedChainId, addressesByNetwork } from "@looksrare/sdk";
import ms from "ms";
import { SetPriceMethod, WarningType } from "nft/components/profile/list/shared";
import { useNFTList, useSellAsset } from "nft/hooks";
import {
  LOOKSRARE_MARKETPLACE_CONTRACT_1155,
  LOOKSRARE_MARKETPLACE_CONTRACT_721
} from "nft/queries/looksRare/constants";
import { OPENSEA_CROSS_CHAIN_CONDUIT } from "nft/queries/openSea/constants";
import { X2Y2_TRANSFER_CONTRACT_1155, X2Y2_TRANSFER_CONTRACT_721 } from "nft/queries/x2y2";
import { ListingStatus } from "nft/types";
import { LOOKS_RARE_CREATOR_BASIS_POINTS, approveCollection, signListing } from "nft/utils/listNfts";
import { useEffect } from "react";
import { NftStandard } from "uniswap/src/data/graphql/uniswap-data-api/__generated__/types-and-hooks";
export async function approveCollectionRow(collectionRow, signer, setCollectionStatusAndCallback) {
  const callback = () => approveCollectionRow(collectionRow, signer, setCollectionStatusAndCallback);
  setCollectionStatusAndCallback(collectionRow, ListingStatus.SIGNING, callback);
  const { marketplace, collectionAddress, nftStandard } = collectionRow;
  const addresses = addressesByNetwork[SupportedChainId.MAINNET];
  const spender = marketplace.name === "OpenSea" ? OPENSEA_CROSS_CHAIN_CONDUIT : marketplace.name === "LooksRare" ? collectionRow.nftStandard === NftStandard.Erc721 ? LOOKSRARE_MARKETPLACE_CONTRACT_721 : LOOKSRARE_MARKETPLACE_CONTRACT_1155 : marketplace.name === "X2Y2" ? collectionRow.nftStandard === NftStandard.Erc721 ? X2Y2_TRANSFER_CONTRACT_721 : X2Y2_TRANSFER_CONTRACT_1155 : addresses.TRANSFER_MANAGER_ERC721;
  !!collectionAddress && await approveCollection(
    spender,
    collectionAddress,
    signer,
    (newStatus) => setCollectionStatusAndCallback(collectionRow, newStatus, callback),
    nftStandard
  );
}
export async function signListingRow(listing, signer, provider, getLooksRareNonce, setLooksRareNonce, setListingStatusAndCallback) {
  const looksRareNonce = getLooksRareNonce();
  const callback = () => {
    return signListingRow(listing, signer, provider, getLooksRareNonce, setLooksRareNonce, setListingStatusAndCallback);
  };
  setListingStatusAndCallback(listing, ListingStatus.SIGNING, callback);
  const { asset, marketplace } = listing;
  const res = await signListing(
    marketplace,
    asset,
    signer,
    provider,
    looksRareNonce,
    (newStatus) => setListingStatusAndCallback(listing, newStatus, callback)
  );
  res && listing.marketplace.name === "LooksRare" && setLooksRareNonce(looksRareNonce + 1);
}
export const getTotalEthValue = (sellAssets) => {
  const total = sellAssets.reduce((total2, asset) => {
    if (asset.newListings?.length) {
      const maxListing = asset.newListings.reduce((a, b) => (a.price ?? 0) > (b.price ?? 0) ? a : b);
      const maxFee = maxListing.marketplace.fee + (maxListing.marketplace.name === "LooksRare" ? LOOKS_RARE_CREATOR_BASIS_POINTS : asset?.basisPoints ?? 0) / 100;
      return total2 + (maxListing.price ?? 0) - (maxListing.price ?? 0) * (maxFee / 100);
    }
    return total2;
  }, 0);
  return total ? Math.round(total * 1e4 + Number.EPSILON) / 1e4 : 0;
};
const getListings = (sellAssets) => {
  const newCollectionsToApprove = [];
  const newListings = [];
  sellAssets.forEach((asset) => {
    asset.marketplaces?.forEach((marketplace) => {
      const newListing = {
        image: asset.smallImageUrl,
        name: asset.name || `#${asset.tokenId}`,
        status: ListingStatus.DEFINED,
        asset,
        marketplace,
        price: asset.newListings?.find((listing) => listing.marketplace.name === marketplace.name)?.price
      };
      newListings.push(newListing);
      if (!newCollectionsToApprove.some(
        (collectionRow) => collectionRow.collectionAddress === asset.asset_contract.address && collectionRow.marketplace.name === marketplace.name
      )) {
        const newCollectionRow = {
          image: asset.asset_contract.image_url,
          name: asset.asset_contract.name,
          status: ListingStatus.DEFINED,
          collectionAddress: asset.asset_contract.address,
          isVerified: asset.collectionIsVerified,
          marketplace,
          nftStandard: asset.asset_contract.tokenType
        };
        newCollectionsToApprove.push(newCollectionRow);
      }
    });
  });
  return [newCollectionsToApprove, newListings];
};
export const verifyStatus = (status) => {
  return status !== ListingStatus.PAUSED && status !== ListingStatus.APPROVED;
};
export function useSubscribeListingState() {
  const sellAssets = useSellAsset((state) => state.sellAssets);
  const { setListings, setCollectionsRequiringApproval } = useNFTList(
    ({ setListings: setListings2, setCollectionsRequiringApproval: setCollectionsRequiringApproval2 }) => ({
      setListings: setListings2,
      setCollectionsRequiringApproval: setCollectionsRequiringApproval2
    })
  );
  useEffect(() => {
    const [newCollectionsToApprove, newListings] = getListings(sellAssets);
    setListings(newListings);
    setCollectionsRequiringApproval(newCollectionsToApprove);
  }, [sellAssets, setCollectionsRequiringApproval, setListings]);
}
export function useHandleGlobalPriceToggle(globalOverride, setListPrice, setPrice, listPrice, globalPrice) {
  useEffect(() => {
    let price;
    if (globalOverride) {
      if (!listPrice) {
        setListPrice(globalPrice);
      }
      price = globalPrice;
    } else {
      price = listPrice;
    }
    setPrice(price);
  }, [globalOverride]);
}
export function useSyncPriceWithGlobalMethod(asset, setListPrice, setGlobalPrice, setGlobalOverride, listPrice, globalPrice, globalPriceMethod) {
  useEffect(() => {
    if (globalPriceMethod === SetPriceMethod.FLOOR_PRICE) {
      setListPrice(asset?.floorPrice);
      setGlobalPrice(asset.floorPrice);
    } else if (globalPriceMethod === SetPriceMethod.LAST_PRICE) {
      setListPrice(asset.lastPrice);
      setGlobalPrice(asset.lastPrice);
    } else if (globalPriceMethod === SetPriceMethod.SAME_PRICE) {
      listPrice && !globalPrice ? setGlobalPrice(listPrice) : setListPrice(globalPrice);
    }
    setGlobalOverride(false);
  }, [globalPriceMethod]);
}
export function useUpdateInputAndWarnings(setWarningType, inputRef, asset, listPrice) {
  useEffect(() => {
    setWarningType(WarningType.NONE);
    const price = listPrice ?? 0;
    inputRef.current.value = `${price}`;
    if (price < (asset?.floorPrice ?? 0) && price > 0) {
      setWarningType(WarningType.BELOW_FLOOR);
    } else if (asset.floor_sell_order_price && price >= asset.floor_sell_order_price && asset.asset_contract.tokenType !== NftStandard.Erc1155) {
      setWarningType(WarningType.ALREADY_LISTED);
    }
  }, [
    asset.asset_contract.tokenType,
    asset?.floorPrice,
    asset.floor_sell_order_price,
    inputRef,
    listPrice,
    setWarningType
  ]);
}
export const getRoyalty = (listingMarket, asset) => {
  const baseFee = listingMarket.name === "LooksRare" ? asset.basisPoints ? LOOKS_RARE_CREATOR_BASIS_POINTS : 0 : asset.basisPoints ?? 0;
  return baseFee * 0.01;
};
const BELOW_FLOOR_PRICE_THRESHOLD = 0.8;
export const findListingIssues = (sellAssets) => {
  const missingExpiration = sellAssets.some((asset) => {
    return asset.expirationTime != null && (isNaN(asset.expirationTime) || asset.expirationTime * 1e3 - Date.now() < ms(`60s`));
  });
  const overMaxExpiration = sellAssets.some((asset) => {
    return asset.expirationTime != null && asset.expirationTime * 1e3 - Date.now() > ms(`180d`);
  });
  const listingsMissingPrice = [];
  const listingsBelowFloor = [];
  const listingsAboveSellOrderFloor = [];
  for (const asset of sellAssets) {
    if (asset.newListings) {
      for (const listing of asset.newListings) {
        if (!listing.price) {
          listingsMissingPrice.push([asset, listing]);
        } else if (listing.price < (asset?.floorPrice ?? 0) * BELOW_FLOOR_PRICE_THRESHOLD && !listing.overrideFloorPrice) {
          listingsBelowFloor.push([asset, listing]);
        } else if (asset.floor_sell_order_price && listing.price >= asset.floor_sell_order_price && asset.asset_contract.tokenType !== NftStandard.Erc1155) {
          listingsAboveSellOrderFloor.push([asset, listing]);
        }
      }
    }
  }
  return {
    missingExpiration,
    overMaxExpiration,
    listingsMissingPrice,
    listingsBelowFloor,
    listingsAboveSellOrderFloor
  };
};
